.link {
    /* font-size: 2em; */
    /* font-weight: 300; */
    cursor: pointer;
    display: inline-block;
    position: relative;
    /* margin-bottom: 0.5em; */
}

.link.anim-destination {
    display: inline-block;
    overflow: hidden;
    /* padding-right: 0.5em; */
    /* font-size: 2em; */
}

.link.anim-destination span {
    position: relative;
    display: inline-block;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.link.anim-destination span::before {
    position: absolute;
    white-space: nowrap;
    top: 100%;
    content: attr(data-hover);
    font-weight: 500;
}

.link.anim-destination:hover span, .link.anim-destination:focus span {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
}

.link.anim-test {
    font-weight: 400;
    color: #ddd;
}

.link.anim-test:after {
    position: absolute;
    content: attr(data-hover);
    color: black;
    top: 0em;
    display: block;
    transition: all .5s ease;
}

.link.anim-test:hover:after {
    opacity: 0;
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
}

.link.anim-iamhere {
    transition: all 0.3s ease;
}

.link.anim-iamhere:hover {
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    color: #e0e0e0;
}

.link.anim-classic {
    border-bottom: 3px solid transparent;
    transition: all .2s ease;
}

.link.anim-classic:hover {
    border-bottom: 3px solid #e0e0e0;
}

.link.anim-classicsub {
    border-top: 3px solid transparent;
    border-bottom: 3px solid transparent;
    transition: all .2s ease-in;
}

.link.anim-classicsub:hover {
    border-top: 3px solid #e0e0e0;
    border-bottom: 3px solid #e0e0e0;
}

.link.anim-fancysub:after {
    content: '';
    position: absolute;
    top: -0.6em;
    display: block;
    width: 100%;
    height: 2em;
    border-top: 0.1em solid transparent;
    border-bottom: 0.1em solid transparent;
    background: transparent;
    transition: all .5s ease;
}

.link.anim-fancysub:hover:after {
    border-top: 0.1em solid #e0e0e0;
    border-bottom: 0.1em solid #e0e0e0;
    top: -0.1em;
    height: 1.2em;
}

.link.anim-fancywrench:after {
    content: '';
    position: absolute;
    top: 0em;
    left: -33%;
    display: block;
    width: 150%;
    height: 1em;
    border-right: 0.1em solid transparent;
    border-left: 0.1em solid transparent;
    background: transparent;
    transition: all .5s ease;
}

.link.anim-fancywrench:hover:after {
    border-right: 0.1em solid #e0e0e0;
    border-left: 0.1em solid #e0e0e0;
    left: -4%;
    width: 104%;
}

.link.anim-lefttoright:after {
    content: '';
    display: block;
    width: 0;
    height: 3px;
    background: #e0e0e0;
    transition: all .5s ease;
}

.link.anim-lefttoright:hover:after {
    width: 100%;
}

.link.anim-righttoleft:after {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    width: 0;
    height: 3px;
    background: #e0e0e0;
    transition: all .5s ease;
}

.link.anim-righttoleft:hover:after {
    width: 100%;
}

.link.anim-middleout:after {
    content: '';
    display: block;
    width: 0;
    height: 3px;
    background: #e0e0e0;
    margin: auto;
    transition: all .5s ease;
}

.link.anim-middleout:hover:after {
    width: 100%;
}


